import Vue from 'vue';
import Floating from 'vue-floating';
import Scroll from 'vue-custom-scrollview';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.use(Floating);
Vue.use(Scroll);
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
