import Vue from 'vue';
import Router from 'vue-router';
import Floating from './views/Floating.vue';
import Scroll from './views/Scroll.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'floating',
      component: Floating,
    },
    {
      path: '/vue-floating',
      name: 'vue-floating',
      component: Floating,
    },
    {
      path: '/vue-custom-scrollview',
      name: 'scroll',
      component: Scroll,
    },
  ],
});
